@media only screen and (max-width: 920px) {
    .container {
        grid-template-columns: 1fr 1fr;
    }

    footer .finished {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
        grid-template-areas: 'texto texto'
                            'img social';
    }

    .character {
        grid-template-areas: 'title title'
                            'figure figure'
                            'mytable mytable'
    }
}

@media only screen and (max-width: 650px) {
    .container {
        grid-template-columns: 1fr;
    }

    .home h1 {
        font-size: 2em;
    }

    .home .options {
        flex-direction: column;
    }

    .option {
        width: 100%;
        max-width: 300px;
        margin-bottom: 25px;
    }

    .lds-grid {
        grid-column-start: 1;
    }

    header div {
        display: flex;
        justify-content: center;
    }

    div > .logo {
        margin: 0;
    }

    nav .meeseks {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: fixed;
        right: 25px;
        bottom: 25px;
        box-shadow: 5px 5px 15px rgba(255,255,255,.7);
        background-color: #99EDE0;
        background-color: var(--primary-color);
        cursor: pointer;
        z-index: 999;
    }

    nav .meeseks:hover {
        background-color: #66CACF;
        background-color: var(--secondary-color);
    }

    .meeseks:nth-child(1) {
        font-size: 2em;
        color: #1B244D;
        color: var(--text-color);
    }

    nav .nav-links {
        position: fixed;
        background-color: #99EDE0;
        background-color: var(--primary-color);
        margin: 0;
        height: 100vh;
        width: 100%;
        left: 0;
        top: 0;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transition: all .5s ease-out;
        z-index: 500;
        clip-path: circle(0px at 85% 92%);
        -webkit-clip-path: circle(0px at 85% 92%);
        pointer-events: none;
    }

    nav .nav-links.active {
        clip-path: circle(1000px at 85% 92%);
        -webkit-clip-path: circle(1000px at 85% 92%);
        pointer-events: all;
    }

    nav .nav-links li {
        opacity: 0;
    }

    nav .nav-links li:nth-child(1) {
        transition: all 0.4s ease 0.4s;
    }
    
    nav .nav-links li:nth-child(2) {
        transition: all 0.4s ease 0.2s;
    }

    nav .nav-links li.open {
        opacity: 1;
    }

    footer .finished {
        grid-template-columns: 1fr;
        grid-template-rows: auto;
        grid-template-areas: 'texto' 'social' 'img';
        grid-gap: 20px;
        gap: 20px;
    }
}
